"use client";

import { useParams } from "next/navigation";
import { useEffect, useState } from "react";
import { Box, RotateCcw, ZoomIn, ZoomOut, Maximize, Layers } from "lucide-react";
import { Card, CardContent, Button } from "@/components/ui";
import { ModelViewer } from "@/components/viewer";
import { apiClient } from "@/lib/api-client";
import { useUIStore } from "@/stores/ui-store";
import { useProjectStore } from "@/stores/project-store";

export default function ThreeDViewPage() {
  const params = useParams();
  const { language } = useUIStore();
  const { project, run, setProject } = useProjectStore();
  const isRtl = language === "ar";
  const projectId = Number(params.id);
  const [modelUrl, setModelUrl] = useState<string | null>(null);
  const [selectedFloor, setSelectedFloor] = useState<number | null>(null);

  useEffect(() => {
    const loadProject = async () => {
      const found = await apiClient.getProject(projectId);
      if (found) {
        setProject(found);
      }
    };
    loadProject();
  }, [projectId, setProject]);

  // Load glTF model from artifacts
  useEffect(() => {
    const loadModel = async () => {
      if (!run?.id || !project?.id) return;
      try {
        const artifacts = await apiClient.getRunArtifacts(run.id);
        const gltfArtifact = artifacts.find(
          (a) => a.kind === "gltf" || a.file_name?.endsWith(".glb") || a.file_name?.endsWith(".gltf")
        );
        if (gltfArtifact) {
          const url = apiClient.getFileUrl(project.id, run.id, gltfArtifact.file_name);
          setModelUrl(url);
        }
      } catch (error) {
        console.error("Failed to load model:", error);
      }
    };
    loadModel();
  }, [run?.id, project?.id]);

  return (
    <div className="space-y-4 animate-fade-in">
      {/* Page Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div>
          <p className="text-xs font-bold uppercase tracking-[3px] text-[var(--accent-2)] mb-1">
            {project?.name || (isRtl ? "المشروع" : "Project")}
          </p>
          <h1 className="text-2xl font-bold text-[var(--ink)]">
            {isRtl ? "العرض ثلاثي الأبعاد" : "3D View"}
          </h1>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" size="sm">
            <Layers className="w-4 h-4 mr-2" />
            {isRtl ? "الطوابق" : "Floors"}
          </Button>
          <Button variant="outline" size="sm">
            <Maximize className="w-4 h-4 mr-2" />
            {isRtl ? "ملء الشاشة" : "Fullscreen"}
          </Button>
        </div>
      </div>

      {/* 3D Viewer */}
      <Card className="min-h-[600px]">
        <CardContent className="p-0 relative">
          {/* Canvas Placeholder */}
          <div className="w-full h-[600px] bg-gradient-to-b from-[#e8e4dc] to-[#d4d0c8] rounded-[var(--radius-lg)] flex items-center justify-center">
            <div className="text-center text-[var(--ink-faint)]">
              <Box className="w-20 h-20 mx-auto mb-4 opacity-30" />
              <p className="text-lg font-medium">
                {isRtl ? "ابدأ التوليد لعرض النموذج" : "Start generation to view 3D model"}
              </p>
              <p className="text-sm mt-2">
                {isRtl
                  ? "سيتم عرض نموذج glTF هنا"
                  : "glTF model will be rendered here"}
              </p>
            </div>
          </div>

          {/* Controls Overlay */}
          <div className="absolute bottom-4 left-1/2 -translate-x-1/2 flex items-center gap-2 bg-[var(--surface)] rounded-full px-3 py-2 shadow-lg border border-[var(--line)]">
            <button className="p-2 rounded-full hover:bg-[var(--bg-2)] text-[var(--ink-soft)]">
              <ZoomOut className="w-5 h-5" />
            </button>
            <button className="p-2 rounded-full hover:bg-[var(--bg-2)] text-[var(--ink-soft)]">
              <RotateCcw className="w-5 h-5" />
            </button>
            <button className="p-2 rounded-full hover:bg-[var(--bg-2)] text-[var(--ink-soft)]">
              <ZoomIn className="w-5 h-5" />
            </button>
          </div>

          {/* Floor Selector */}
          <div className="absolute right-4 top-1/2 -translate-y-1/2 bg-[var(--surface)] rounded-[var(--radius-md)] p-2 shadow-lg border border-[var(--line)]">
            <div className="space-y-1">
              {Array.from({ length: project?.floors || 5 }, (_, i) => (
                <button
                  key={i}
                  className="w-8 h-8 rounded text-xs font-medium hover:bg-[var(--bg-2)] text-[var(--ink-soft)]"
                >
                  {(project?.floors || 5) - i}
                </button>
              )).slice(0, 5)}
              {(project?.floors || 0) > 5 && (
                <span className="block text-center text-xs text-[var(--ink-faint)]">...</span>
              )}
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
